import React from 'react';
import ReactPageScroller from 'react-page-scroller';
import { AboutVDE } from './about-vde/about-vde.component';
import { OurValues } from './our-values/our-values.component';
import { Sharing } from './sharing/sharing.component';
import SmoothScrollContainer from '../../ui/smooth-scroll-container/smooth-scroll-container.component';
import { CSSTransition } from 'react-transition-group';

const Animate = ({ transitionIn, transition = 'fade', ...props }) => (
  <CSSTransition
    in={transitionIn}
    unmountOnExit
    timeout={2000}
    classNames={transition}
  >
    <div className={transition} {...props} />
  </CSSTransition>
);

export const AboutContainer = () => {
  return (
    <SmoothScrollContainer pages={3}>
      {({ visible, isOnlyVisible }) => (
        <>
          <Animate transitionIn={isOnlyVisible(0)}>
            <AboutVDE />
          </Animate>
          <Animate transitionIn={isOnlyVisible(1)}>
            <OurValues />
          </Animate>
          <Animate transitionIn={isOnlyVisible(2)} transition="slide">
            <Sharing />
          </Animate>
        </>
      )}
    </SmoothScrollContainer>
  );
};
